.container{display:flex;flex-direction:column;align-items:center;width:100%}.container .mapContainer{margin-bottom:-3rem;width:110%;height:45rem;display:flex;justify-content:flex-end;align-items:center}.container .stats{display:flex;width:100%;justify-content:space-between}.container .stats .progressContainer{width:12rem;position:relative;transition:.3s}.container .stats .progressContainer .countContainer{display:flex;align-items:flex-end}.container .stats .progressContainer .countContainer .count{font-size:3.5rem;color:#24292f;margin:0 -0.3rem .3rem 0;font-weight:400}.container .stats .progressContainer .countContainer .totalCount{font-size:1.75rem;color:#24292f;font-weight:400}.container .stats .progressContainer .type{margin-top:.5rem;font-size:1.4rem;font-weight:400;text-transform:uppercase;text-align:center;max-height:1.4rem}.container .stats .progressContainer .unselected{position:absolute;top:25%;left:50%;transform:translateX(-50%) scale(1);z-index:-100}.container .stats .progressContainer .progressbarBackground{position:absolute;top:0;left:0;width:100%;height:100%;border-radius:50%;background-color:#fff;opacity:.5;transform:scale(1)}.container .stats .progressContainer .progressbarBackground.selected{opacity:0;cursor:pointer}.container .stats .selected{transform:scale(1.25)}