.container{width:100vw;margin-top:-9rem;height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.container .deadEndSign{width:14rem;aspect-ratio:1;background-color:#eed202;display:flex;justify-content:center;align-items:center;border-radius:1rem;box-shadow:inset 0 0 0 .2rem #eed202,inset 0 0 0 .5rem #24292f;transform:rotate(45deg)}.container .deadEndSign p{text-transform:uppercase;font-size:3.5rem;text-align:center;transform:rotate(-45deg)}.container h1{margin-top:5rem;font-size:2.5rem;font-weight:400;color:#24292f}.container button{margin-top:2rem;padding:0 2.5rem;border-radius:2.25rem;height:4.5rem;cursor:pointer;background:#319fff;text-align:center;color:#fff;text-transform:uppercase;font-size:2rem;font-weight:500;transition:transform .3s}.container button:hover{transform:scale(1.1)}